/* styles/markdown.css */

#react-markdown {
  /* Optionally, you can do something like a "reset" or base font: */
  font-size: 1rem;
  line-height: 1.5rem;
  line-height: 1.625;
}

/* Headings */
#react-markdown h1 {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 700;
  line-height: 1.25;
}

#react-markdown h2 {
  margin-top: 1.25rem;
  margin-bottom: 0.75rem;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
  line-height: 1.25;
}

#react-markdown h3 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 600;
  line-height: 1.25;
}

/* Paragraphs */
#react-markdown p {
  margin-bottom: 1rem;
}

/* Lists */
#react-markdown ul {
  margin-left: 1.5rem;
  margin-bottom: 1rem;
  list-style-type: disc;
}

#react-markdown ol {
  margin-left: 1.5rem;
  margin-bottom: 1rem;
  list-style-type: decimal;
}

#react-markdown li {
  margin-bottom: 0.25rem;
}

/* Links */
#react-markdown a {
  --tw-text-opacity: 1;
  color: rgb(37 99 235 / var(--tw-text-opacity, 1));
  text-decoration-line: underline;
}

/* Blockquotes */
#react-markdown blockquote {
  margin-bottom: 1rem;
  border-left-width: 4px;
  --tw-border-opacity: 1;
  border-color: rgb(209 213 219 / var(--tw-border-opacity, 1));
  padding-left: 1rem;
  font-style: italic;
  --tw-text-opacity: 1;
  color: rgb(55 65 81 / var(--tw-text-opacity, 1));
}

/* Horizontal rule */
#react-markdown hr {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  border-top-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(209 213 219 / var(--tw-border-opacity, 1));
}

/* Inline code */
#react-markdown code {
  border-radius: 0.25rem;
  --tw-bg-opacity: 1;
  background-color: rgb(243 244 246 / var(--tw-bg-opacity, 1));
  padding-left: 0.25rem;
  padding-right: 0.25rem;
  padding-top: 0.125rem;
  padding-bottom: 0.125rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

/* Code blocks */
#react-markdown pre code {
  margin-bottom: 1rem;
  display: block;
  border-radius: 0.25rem;
  --tw-bg-opacity: 1;
  background-color: rgb(243 244 246 / var(--tw-bg-opacity, 1));
  padding: 1rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  /* If you want to preserve horizontal scrolling: */
  overflow-x: auto;
}
